import './reset.css';
import './App.scss';
import { useEffect, useState } from 'react';

//components
import Nav from './components/Nav';
import MoonIntro from './components/MoonIntro';
import MoonTerrain from './components/MoonTerrain';
import MoonCard from './components/MoonCard';
import Moon3d from './components/Moon3D';
import Chatroom from './components/Chatroom';




function App() {
  // navigator
  const [activeNav, setActiveNav] = useState(0);
  const [showContents, setShowContents] = useState(0);

  //scroll position
  const handleFollow = () => {
    const section0OffsetBotton = document.querySelector('#section0').offsetTop + document.querySelector('#section0').clientHeight;
    const section1OffsetBotton = document.querySelector('#section1').offsetTop + document.querySelector('#section1').clientHeight;
    const section2OffsetBotton = document.querySelector('#section2').offsetTop + document.querySelector('#section2').clientHeight;
    const section3OffsetBotton = document.querySelector('#section3').offsetTop + document.querySelector('#section3').clientHeight;
    const section4OffsetBotton = document.querySelector('#section4').offsetTop + document.querySelector('#section4').clientHeight;

    if (window.scrollY < section0OffsetBotton) {
      setActiveNav(0);
      setShowContents(0);
    } else if(window.scrollY < section1OffsetBotton) {
      setActiveNav(1);
      setShowContents(1);
    } else if(window.scrollY < section2OffsetBotton) {
      setActiveNav(2);
      setShowContents(2);
    } else  if(window.scrollY < section3OffsetBotton) {
      setActiveNav(3);
      setShowContents(3);
    } else if(window.scrollY < section4OffsetBotton) {
      setActiveNav(4);
      setShowContents(4);
    };
  };

  useEffect(()=>{
    window.addEventListener('scroll', handleFollow);
    return () => {
      window.removeEventListener('scroll', handleFollow);
    }
  });

  return (
    <div className="App">
      <Nav activeNav={activeNav} />

      <main>
          <MoonIntro showContents={showContents} />

          <div className='container'>
            <MoonTerrain showContents={showContents} />
            <MoonCard />
            <Moon3d />
            <Chatroom />
          </div>
      </main>

    </div>
  );
}



export default App;
